.App {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(450px, 1fr) ) ;
  grid-gap: 25px;
  gap: 25px;
}

.main-header--container {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  text-align: center;
  color:  #5c5959;
}

.main-header--container > p {
  text-align: center;
  margin-top: -20px;
  padding-bottom: 10px;
}

.main-header--container > p > a{
  text-align: center;
  text-decoration: none;
  color:  #5c5959;
  
}

.main-header--container > p > a:hover{
  text-align: center;
  text-decoration: underline;
  -webkit-text-decoration-color: #5c5959;
          text-decoration-color: #5c5959;
  font-size: 1.10rem;
  
}

.form--outputs {
  border: 5px solid #D3D3D3;
  min-width: 55vw;
}

.intro--container {
    background-color: rgb(157, 212, 222);
    border: none;
    color:  #5c5959;
    font-family: 'Montserrat', sans-serif;
    padding: 25px;
    text-align: center;
}

.contact--container {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    background-color: #5c5959;
    color: white;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 2px;
}

.fullName {
    font-size: 20px;
}

.summary {
    color:  #5c5959;
    font-style: italic;
    padding: 20px;
    text-align: center;
}

.image {
    -webkit-filter: invert(100%); /* Safari/Chrome */
    filter: invert(100%);
    width: 20px;
    height: 20px;
    margin-bottom: -5px;
    padding-right: 5px;
}






.exp--container h2 {
    background-color: #5c5959;
    color: white;
    font-family: 'Krub', sans-serif;
    letter-spacing: 5px;
    padding: 15px;
    text-transform: uppercase;
}

.expContent---container {
    color: #5c5959;
    font-family: 'Montserrat', sans-serif;
    padding: 25px;
}

.dates--container {
    display: flex;
    align-items: center;
    text-align: center;
    margin-top: -20px;
    margin-bottom: -20px;
}

.expName {
    font-size: 1.5rem;
}

.expJobTitle {
    font-size: 1.2rem;
}

.divider {
    font-size: 15px;
    font-weight: 700;
    color: #888888;
    padding: 0 10px;
}
.edu--container h2 {
    background-color: #5c5959;
    color: white;
    font-family: 'Krub', sans-serif;
    letter-spacing: 5px;
    padding: 15px;
    text-transform: uppercase;
}

.eduContent--container {
    color: #5c5959;
    font-family: 'Montserrat', sans-serif;
    padding: 10px 25px;
}

.eduDegree,
.eduField {
    font-weight: 700;
}
.ref--container p {
    font-family: 'Montserrat', sans-serif;
    padding: 20px;
}

.ref--container h2 {
    background-color: #5c5959;
    color: white;
    font-family: 'Krub', sans-serif;
    letter-spacing: 5px;
    padding: 15px;
    text-transform: uppercase;
}

.ref--text {
    color: #5c5959;
    font-style: italic;
}
/* FORM INPUTS STYLING */
.label--text {
    color: #5c5959;
    font-size: 12.5px;
}

legend {
    color:  #5c5959;
    font-size: 1.5rem;
    font-weight: 700;
}

fieldset {
    border: solid 5px #D3D3D3;
    margin: 10px;
    padding: 15px;
}

input {
    color: #5c5959;
    width: 100%;
    padding: 10px 0;
    height: 25px;
    margin-top: 5px;
    text-indent: 10px;
    font-size: 1.25rem;
    border-radius: 5px;
    border: solid 1.5px #D3D3D3; /* Safari */
    transition: 1s;
  }
  input[type=text]:hover{
    box-shadow: 0 0 5pt 0.5pt #D3D3D3;
  }
  input[type=text]:focus {
    box-shadow: 0 0 5pt 2pt #D3D3D3;
    outline-width: 0px;
  }

  input[type=date] {
    color: #5c5959;
  }

  input[type=date]:hover{
    box-shadow: 0 0 5pt 0.5pt #D3D3D3;
  }
  input[type=date]:focus {
    box-shadow: 0 0 5pt 2pt #D3D3D3;
    outline-width: 0px;
  }

  textarea {
    color: #5c5959;
    width: 100%;
    padding: 10px 0;
    height: 25px;
    margin-top: 5px;
    text-indent: 10px;
    font-size: 1.25rem;
    border-radius: 5px;
    border: solid 1.5px #D3D3D3; /* Safari */
    transition: 1s;
    resize: vertical;
  }

  textarea:focus {
    box-shadow: 0 0 5pt 2pt #D3D3D3;
    outline-width: 0px;
  }
